<template>
    <div>
        <div class="cell-info">
            <div class="cell-info-left">
                <div class="img-container">
                    <img v-lazy="cellData.headimgurl" class="img" />
                </div>
                <div class="cell-info-middle">
                    <p class="name">{{cellData.username}}</p>
                    <p class="icon-phone iconfont"><span class="under-phone">{{cellData.phone}}</span></p>
                </div>
            </div>
            
            <div class="cell-info-right">
                <template v-if="cellType!='list'">
                    <template v-if="!cellData.bindMe">
                        <mt-button size="small" class="handle-btn" type="default" @click="bindHandle">绑定美体师</mt-button>
                    </template>
                    <template v-else>
                        <div class="has-bind-tip">已绑定</div>
                    </template>
                </template>
                <template v-else>
                    <mt-button size="small" class="handle-btn" type="default" @click="cancelBindHandle">解绑美体师</mt-button>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
Vue.component(Button.name, Button);
import bus from "../../../../common/js/eventBus.js";

export default {
  name: "therapist-cell",
  data() {
    return {};
  },
  props: {
    cellType:{
       type:String,
       default:'list'             //是列表还是显示结果
    },
    cellData: {
      type: Object,
      default: function() {
        return {
          headimgurl: "",
          phone: 4001368868,
          username: "L先生",
          bindMe:true,
          role:1,
          beautiesid: ""
        };
      }
    }
  },
  components: {},
  activated() {},
  computed: {
    ...mapState(["openid", "path",'userid'])
  },
  methods: {
    bindHandle() {
      if(this.cellData.beautiesid==this.userid){
          this.showMsg('不能绑定自己');
          return;
      }
      let roleStr=this.cellData.role.toString();
      if(roleStr=='2'){
          this.showMsg('该用户是商家');
          return;
      }
      if(roleStr=='3'){
          this.showMsg('该用户是业务员');
          return;
      }
      if(roleStr=='4'){
          this.showMsg('该用户是美导');
          return;
      }
      if(roleStr=='5'){
          this.showMsg('该用户是代理商');
          return;
      }
      bus.$emit("therapis-bind-call", this.cellData);
    },
    cancelBindHandle() {
      bus.$emit("therapis-bind-cancel-call", this.cellData);
    },
    showMsg(msg){
        Toast({message: msg,duration: 1500});
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.cell-info {
    width: 100%;
    height: .75rem;
    font-size: 0;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .cell-info-left {
        display: flex;
        align-items: center;

        .img-container {
            margin: 0 .1rem;
            display: inline-block;
            width: .55rem;
            height: .55rem;

            .img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                position: relative;
            }
        }

        .cell-info-middle {
            flex: 1;

            .name {
                max-width: 1.2rem;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                display: inline-block;
                font-size: .15rem;
                font-weight: 700;
                color: #42495C;
                padding-top: 0.03rem;
            }

            .icon-phone {
                position: relative;
                left: -0.01rem;
                font-size: .14rem;
                color: #878a94;
                margin-top: 0.06rem;

                .under-phone {
                    margin-left 0.01rem;
                    color: #aaa;
                    font-size: .13rem;
                }
            }

        }
    }

    .cell-info-right {
        flex: 0 0 1.06rem;
        text-align: center;

        .handle-btn {
            margin: .05rem 0;
            height: .24rem;
            font-size: .12rem;
            color: #42495C;
            background-color: #eee;
        }
        .has-bind-tip{
            color #999;
            font-size .13rem;
            letter-spacing 0.01rem;
        }
    }
}
</style>



